.stock-essence {
  background-color: #f5f5f7;
  min-height: 100vh;
  padding: 40px 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
}

.stock-essence h1 {
  text-align: center;
  margin-bottom: 20px;
}

.stock-essence button {
  margin-bottom: 20px;
}

.stock-item {
  background-color: #f5f5f5;
  border-radius: 5px;
  padding: 15px;
  margin-bottom: 15px;
}

.stock-item h2 {
  margin-top: 0;
}

.se-header {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 30px;
  text-align: left;
}

.se-title {
  font-size: 48px;
  font-weight: bold;
  margin-bottom: 20px;
  color: #1d1d1f;
}

.se-title-highlight {
  color: #0071e3;
}

.se-back-button {
  background-color: #0071e3;
  color: #fff;
  border: none;
  padding: 8px 16px;
  font-size: 14px;
  border-radius: 20px;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-bottom: 30px;
}

.se-back-button:hover {
  background-color: #0077ed;
}

.se-content {
  display: flex;
  gap: 40px;
}

.se-search-section {
  flex: 0 0 40%;
  position: relative;
  text-align: left;
}

.se-search-input {
  width: 100%;
  padding: 12px;
  font-size: 16px;
  border: 1px solid #d2d2d7;
  border-radius: 8px;
  background-color: #fff;
}

.se-search-results {
  position: absolute;
  width: 100%;
  background-color: white;
  border: 1px solid #d2d2d7;
  border-top: none;
  border-radius: 0 0 8px 8px;
  list-style-type: none;
  padding: 0;
  margin: 0;
  max-height: 300px;
  overflow-y: auto;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.se-search-results li {
  padding: 10px;
  cursor: pointer;
}

.se-search-results li:hover {
  background-color: #f5f5f7;
}

.se-results-section {
  flex: 1;
}

.se-results {
  background-color: #fff;
  padding: 30px;
  border-radius: 18px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.se-results h2 {
  font-size: 24px;
  font-weight: 600;
  color: #1d1d1f;
  margin-bottom: 20px;
  text-align: center;
}

.se-result-item {
  display: flex;
  justify-content: space-between;
  margin-bottom: 10px;
  padding: 10px 0;
  border-bottom: 1px solid #d2d2d7;
}

.se-result-item:last-child {
  border-bottom: none;
}

.se-result-label {
  color: #86868b;
  font-size: 14px;
  text-align: left;
}

.se-result-value {
  font-weight: 600;
  color: #1d1d1f;
  font-size: 16px;
  text-align: right;
  word-break: break-word;
  max-width: 60%;
}

.se-error {
  color: #ff3b30;
  margin-top: 20px;
  text-align: center;
}

@media (max-width: 768px) {
  .se-title {
    font-size: 32px;
  }

  .se-content {
    flex-direction: column;
  }

  .se-search-section {
    flex: 1 1 auto;
    width: 100%;
  }

  .se-search-input {
    width: 100%;
    box-sizing: border-box;
  }
}

.search-container {
  position: relative;
  margin-bottom: 20px;
}

.search-input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
}

.dropdown-menu {
  position: absolute;
  top: 100%;
  left: 0;
  right: 0;
  background-color: white;
  border: 1px solid #ddd;
  border-top: none;
  list-style-type: none;
  padding: 0;
  margin: 0;
  max-height: 200px;
  overflow-y: auto;
  z-index: 1000;
}

.dropdown-menu li {
  padding: 10px;
  cursor: pointer;
}

.dropdown-menu li:hover {
  background-color: #f0f0f0;
}

